input[type="file"] {
    display: none;
}
.file-upload{
    width: 310px;
    height: 310px;
    background: #FAFAFA;
    border: 1px solid #000000;
    box-shadow: 0px 31px 80px rgba(0, 0, 0, 0.0196802), 0px 12.9511px 33.4221px rgba(0, 0, 0, 0.0282725), 0px 6.92426px 17.869px rgba(0, 0, 0, 0.035), 0px 3.88168px 10.0172px rgba(0, 0, 0, 0.0417275), 0px 2.06153px 5.32008px rgba(0, 0, 0, 0.0503198), 0px 0.85785px 2.21381px rgba(0, 0, 0, 0.07);
    border-radius: 20px;
